const extend = require('zhf.extend');
const typeOf = require('zhf.type-of');
const queryString = require('zhf.query-string');
const Message = require('zhf.g-ui/dist/js/components_dom/g-message/index.js');

module.exports = function (json) {
    const opts = extend({
        url: '',
        data: {},
        isHandleError: true, // 
        isHandleFailure: true, // 
        callbackName: null, // callback
        callback: function () {
        },
    }, json);
    const url = opts.url;
    const data = opts.data;
    const callbackName = opts.callbackName;
    const callback = opts.callback;

    function fnError() {
        const dataInfo = {
            status: 'error',
            message: '',
            error: 'Request failed with status code 404',
        };
        callback(dataInfo);
        if (opts.isHandleError) {
            new Message({
                config: {
                    content: `: ${dataInfo.error}`,
                },
            });
        }
    }

    if (url) {
        const random = ('' + Math.random()).substring(2);
        let fnName = `jsonpCallback${new Date().getTime()}${random}`;
        if (callbackName && typeOf(callbackName) === 'string') {
            fnName = callbackName;
        }
        window[fnName] = function (dataInfo) {
            callback(dataInfo);
            if (dataInfo.status === 'failure' && opts.isHandleFailure) {
                new Message({
                    config: {
                        content: `: ${dataInfo.message}`,
                    },
                });
            }
        };
        const script = document.createElement('script');
        script.addEventListener('error', function () {
            document.body.removeChild(script);
            fnError();
        });
        script.addEventListener('load', function () {
            document.body.removeChild(script);
        });
        const parameter = queryString.queryStringify(data);
        // jsonp - jsonpget,
        if (parameter) {
            script.src = `${url}?${parameter}&callback=${fnName}`;
        } else {
            script.src = `${url}?callback=${fnName}`;
        }
        document.body.appendChild(script);
    } else {
        fnError();
    }
};



