const axios = require('axios');
const extend = require('zhf.extend');
const typeOf = require('zhf.type-of');
const Message = require('zhf.g-ui/dist/js/components_dom/g-message/index.js');
const qs = require('qs');

module.exports = function (json) {
    json.method = (json.method || json.type || 'GET').toUpperCase(); // $.ajax$.ajaxtype
    const opts = extend({
        method: 'GET', // GET
        timeout: 30000, // 
        isHandleError: true, // 
        isHandleFailure: true, // 
        isHandleSuccess: false, // 
        /*
        >>>
        Promise
        PromisedataInfo.status
        */
        callbackError: function () { // 
        },
        callbackFailure: function () { // 
        },
        callbackSuccess: function () { // 
        },
        callbackComplete: function () { // 
        },
    }, json);
    /*
    * javascript axios GET params
    * javascript axios POST/PUT/DELETE data
    * ,data
    * nodejs express GET req.query
    * nodejs express POST/PUT/DELETE body-parser req.body
    * ,req.data
    * */
    if (opts.method.toUpperCase() === 'GET') {
        opts.params = opts.params || opts.data; // $.ajax$.ajaxdata
    } else {
        // POST,PUT,DELETE$.ajax(application/jsonapplication/x-www-form-urlencoded)
        // GET,GET$.ajaxqs.stringifyaxios$.ajaxGETaxios
        // axiosPOST,PUT,DELETE,axiosGETPOSTaxiosGET
        if (typeOf(opts.data) === 'object') { // undefined,formdata
            opts.data = qs.stringify(opts.data);
        }
    }
    return axios(opts).catch(function (error) {
        const response = {
            data: {
                status: 'error',
                message: error,
            },
        };
        const dataInfo = response.data;
        if (opts.isHandleError) {
            new Message({
                config: {
                    content: response.data.message, // errorError
                },
            });
            (typeof opts.callbackError === 'function') && opts.callbackError(dataInfo);
        }
        return response;
    }).then(function (response) {
        const dataInfo = response.data;
        if (dataInfo.status === 'failure') { // 
            if (opts.isHandleFailure) {
                new Message({
                    config: {
                        content: `: ${dataInfo.message}`,
                    },
                });
            }
            (typeof opts.callbackFailure === 'function') && opts.callbackFailure(dataInfo);
        }
        if (dataInfo.status === 'success') { // 
            if (opts.isHandleSuccess) {
                new Message({
                    config: {
                        content: `: ${dataInfo.message}`,
                    },
                });
            }
            (typeof opts.callbackSuccess === 'function') && opts.callbackSuccess(dataInfo);
        }
        (typeof opts.callbackComplete === 'function') && opts.callbackComplete(dataInfo);
        return dataInfo;
    });
};



